import Stream from 'avutil/AVStream'
import { MOVContext, MOVStreamContext } from '../type'
import IOWriter from 'common/io/IOWriterSync'
import { BoxType, TFHDFlags } from '../boxType'

export default function write(ioWriter: IOWriter, stream: Stream, movContext: MOVContext) {
  const track = movContext.currentFragment.tracks.find((track) => {
    return track.trackId === (stream.privData as MOVStreamContext).trackId
  })

  let flags = TFHDFlags.BASE_DATA_OFFSET
    | TFHDFlags.SAMPLE_DURATION
    | TFHDFlags.SAMPLE_SIZE
    | TFHDFlags.SAMPLE_FLAGS

  if (track.baseIsMoof) {
    flags &= ~TFHDFlags.BASE_DATA_OFFSET
    flags |= TFHDFlags.DEFAULT_BASE_IS_MOOF
  }

  const pos = ioWriter.getPos()

  // size
  ioWriter.writeUint32(0)
  // tag
  ioWriter.writeString(BoxType.TFHD)

  // version use int64
  ioWriter.writeUint8(0)
  // flags
  ioWriter.writeUint24(flags)

  ioWriter.writeUint32(track.trackId)
  if (flags & TFHDFlags.BASE_DATA_OFFSET) {
    ioWriter.writeUint64(track.baseDataOffset)
  }
  if (flags & TFHDFlags.SAMPLE_DURATION) {
    ioWriter.writeUint32(track.defaultSampleDuration)
  }
  if (flags & TFHDFlags.SAMPLE_SIZE) {
    ioWriter.writeUint32(track.defaultSampleSize)
  }
  if (flags & TFHDFlags.SAMPLE_FLAGS) {
    ioWriter.writeUint32(track.defaultSampleFlags)
  }

  movContext.boxsPositionInfo.push({
    pos,
    type: BoxType.TFHD,
    size: Number(ioWriter.getPos() - pos)
  })
}
