import { AVMediaType } from 'avutil/codec'
import { BoxType } from './boxType'
import { FragmentTrack, MOVContext } from './type'

export interface BoxLayout {
  type: BoxType,
  children?: BoxLayout[]
}

function getTrackBoxAudioLayout(context: MOVContext) {
  return [
    {
      type: BoxType.TKHD
    },
    {
      type: BoxType.EDTS
    },
    {
      type: BoxType.MDIA,
      children: [
        {
          type: BoxType.MDHD
        },
        {
          type: BoxType.HDLR
        },
        {
          type: BoxType.MINF,
          children: [
            {
              type: BoxType.SMHD
            },
            context.isom
              ? {
                type: BoxType.MINF_HDLR
              }
              : null,
            {
              type: BoxType.DINF,
              children: [
                {
                  type: BoxType.DREF,
                  children: [
                    {
                      type: BoxType.URL
                    }
                  ]
                }
              ]
            },
            {
              type: BoxType.STBL,
              children: [
                {
                  type: BoxType.STSD
                },
                {
                  type: BoxType.STTS
                },
                {
                  type: BoxType.STSC
                },
                {
                  type: BoxType.STSZ
                },
                {
                  type: context.use64Mdat ? BoxType.CO64 : BoxType.STCO
                }
              ]
            }
          ]
        }
      ]
    }
  ] as BoxLayout[]
}

