import { MOVContext } from '../type'
import { NOPTS_VALUE, NOPTS_VALUE_BIGINT } from 'avutil/constant'

export default function createMovContext(): MOVContext {
  return {
    isom: false,
    timescale: NOPTS_VALUE,
    duration: NOPTS_VALUE_BIGINT,
    foundMoov: false,
    foundMdat: false,
    majorBrand: 0,
    minorVersion: 0,
    compatibleBrand: [],
    creationTime: 0n,
    modificationTime: 0n,
    rate: NOPTS_VALUE,
    volume: NOPTS_VALUE,
    matrix: null,
    nextTrackId: 1,
    fragment: false,
    trexs: [],
    currentFragment: null,
    boxsPositionInfo: [],
    holdMoovPos: 0n,
    currentChunk: null
  }
}
