import * as is from 'common/util/is'

export default function digital2Tag(value: number | bigint, len: number = 4) {
  let tag = ''
  for (let i = 0; i < len; i++) {
    if (is.number(value)) {
      tag = String.fromCharCode(value & 0xff) + tag
      value >>>= 8
    }
    else {
      tag = String.fromCharCode(Number(value & 0xffn)) + tag
      value >>= 8n
    }
  }
  return tag
}
