import { NOPTS_VALUE_BIGINT } from 'avutil/constant'
import { FragmentTrack } from '../type'

export default function createFragmentTrack(): FragmentTrack {
  return {
    trackId: 0,
    baseDataOffset: 0n,
    defaultSampleDuration: 0,
    defaultSampleSize: 0,
    defaultSampleFlags: 0,
    baseMediaDecodeTime: 0n,
    sampleCount: 0,
    dataOffset: 0,
    remainDataOffsets: [],
    remainDataOffsetIndex: [],
    dataOffsetPos: 0n,
    firstSampleFlags: 0,
    sampleDurations: [],
    sampleSizes: [],
    sampleFlags: [],
    sampleCompositionTimeOffset: [],
    baseIsMoof: false,
    ioWriter: null,
    buffers: [],
    lastFragIndexDts: 0n,
    tfdtDelay: NOPTS_VALUE_BIGINT,
    trunPtsDelay: 0n
  }
}
