import Stream from 'avutil/AVStream'
import { MOVContext } from '../type'
import IOWriter from 'common/io/IOWriterSync'
import { BoxType } from '../boxType'
import { AVMediaType } from 'avutil/codec'
import { AVStreamMetadataKey } from 'avutil/AVStream'

export default function write(ioWriter: IOWriter, stream: Stream, movContext: MOVContext) {

  const pos = ioWriter.getPos()
  // size
  ioWriter.writeUint32(0)
  // tag
  ioWriter.writeString(BoxType.HDLR)

  // version
  ioWriter.writeUint8(0)
  // flags
  ioWriter.writeUint24(0)

  let hdlr = 'dhlr'
  let hdlrType = 'url '
  let descr = 'DataHandler'

  if (stream) {
    hdlr = 'mhlr'
    if (stream.codecpar.codecType === AVMediaType.AVMEDIA_TYPE_AUDIO) {
      hdlrType = 'soun'
      descr = 'SoundHandler'
    }
    else if (stream.codecpar.codecType === AVMediaType.AVMEDIA_TYPE_VIDEO) {
      hdlrType = 'vide'
      descr = 'VideoHandler'
    }
    else if (stream.codecpar.codecType === AVMediaType.AVMEDIA_TYPE_SUBTITLE) {
      hdlrType = 'text'
      descr = 'SubtitleHandler'
    }
    else {
      if (stream.metadata[AVStreamMetadataKey.HANDLER_NAME]) {
        descr = stream.metadata[AVStreamMetadataKey.HANDLER_NAME]
      }
      if (stream.metadata['hdlrType']) {
        hdlrType = stream.metadata['hdlrType']
      }
    }
  }

  // handler
  ioWriter.writeString(hdlr)
  // handler type 
  ioWriter.writeString(hdlrType)

  // reserved
  ioWriter.writeUint32(0)
  ioWriter.writeUint32(0)
  ioWriter.writeUint32(0)

  if (!stream || movContext.isom) {
    ioWriter.writeUint8(descr.length)
  }

  ioWriter.writeString(descr)

  if (stream && !movContext.isom) {
    // c string
    ioWriter.writeUint8(0)
  }

  movContext.boxsPositionInfo.push({
    pos,
    type: BoxType.HDLR,
    size: Number(ioWriter.getPos() - pos)
  })
}
