import * as logger from 'common/util/logger'

export default function mktag(tag: string): number {
  if (tag.length !== 4) {
    logger.warn(`tag length is not 4, tag: ${tag}`)
  }

  let value = 0
  for (let i = 0; i < 4; i++) {
    value = (value << 8) | tag.charCodeAt(i)
  }

  return value
}
